import { Component, OnInit } from "@angular/core";
import { Commfunc } from "../../../../@uisftech/common/service/commonFun";
import { CustomListPreScreeningBean } from "./bean/custom-list-pre-screening.bean";
import { Message, ConfirmationService } from "primeng/primeng";

@Component({
    selector: 'customListPreScreening',
    templateUrl: './custom-list-pre-screening.component.html',
    styleUrls: ['./custom-list-pre-screening.component.scss'],
    providers: [Commfunc,ConfirmationService]
})
export class CustomListPreScreeningComponent implements OnInit {
    msgs:Message[];
    bean:CustomListPreScreeningBean = new CustomListPreScreeningBean();
    total: string;//总页数
    first: number = 0;
    pageNum: number;//当前页数
    pageSize: number;//每页条数
    tableValueList:any =[{//表格内容
         "x1":"1",
         "x2":"1",
         "x3":"1",
         "x4":"1",
         "x5":"1",
         "x6":"1",
         "x7":"1",
         "x8":"1",
         "x9":"1",
         "x10":"1",
         "x11":"1",
         "x12":"1",
         "x13":"1",
         "x14":"1",
         "x15":"1",
         "x16":"1",
         "x17":"1",
         "x18":"1",
         "x19":"1"
    }];

    constructor(
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService
    ) {
        
    }
    ngOnInit() {
        
    }

    //查询
    query(){

    }

    //翻页
    paginate(event){
        //每页显示的条数
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.first = (this.pageNum - 1) * this.pageSize;
        this.query();
    }

    //详情
    detailsDisplay:boolean = false;
    toDetails(col){
        this.detailsDisplay = true;
    }

    //编辑
    editDisplay:boolean = false;//开关编辑窗口
    customerNoDispaly:boolean = false;//客户号必填 提示开关
    varieties:any = [];//业务品种list
    productName:any = [];//产品名称list
    toEdit(col){
        this.editDisplay = true;
    }

    //删除
    toDelete(col){
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            header: '删除',
			accept: () => {
                
			}
		});
    }

    //修改
    editCustomerNoDispaly:boolean = false;
    editBusinessTypeList:any = [];//业务品种列表
    editProductNameList:any = [];//产品名称列表
    alter(){
        if(this.bean.editCustomerNo==null || this.bean.editCustomerNo=="" || typeof(this.bean.editCustomerNo)=="undefined"){
            this.editCustomerNoDispaly = true;
        }else{
            this.editCustomerNoDispaly = false;
        }
    }

    //重置
    reset(){
        this.editCustomerNoDispaly = false;
        this.bean.editCustomerNo = "";
        this.bean.editCustomerName = "";
        this.bean.editBusinessType = "";
        this.bean.editProductName = "";
        this.bean.editAmount = "";
        this.bean.editInterestRate = "";
        this.bean.editPrimaryContact = "";
        this.bean.editPrimaryContactAgency = "";
        this.bean.editRequestProcessingDate = "";
        this.bean.editContractAmount = "";
        this.bean.editUsedContractAmount = "";
        this.bean.editGetDate = "";
        this.bean.editContractType = "";
        this.bean.editGuaranteeMode = "";
        // this.editBusinessTypeList = [];
        // this.editProductNameList = [];
    }

    //取消
    cancel(){
        this.reset();
        this.editDisplay = false;
    }
}